  text-decoration: none;
  
}
#nav{
  margin-bottom:0;
}
.border{
  border: 1px solid black;
}
.margins{
  margin-left:15px;
  margin-right:10px;
  background:white;
  margin-top:70px;
  
}
@media only screen and (min-width: 600px) {
  .margins {
    margin-left:40px;
    margin-right:40px;
    
  }
}
@media only screen and (min-width: 800px) {
  .margins {
    margin-left:10px;
    margin-right:10px;
    
  }
}
.martop{
  margin-top:70px;
}
.img{
float:left;
width:100%;
margin-bottom:20px;
}
@media only screen and (min-width: 700px) {
 .img{
   width:60%;
   margin-right:10px;
 }
    
  }

.center{
  font-family: monospace;
  text-align:center;
 
  
}



.sticky{position:sticky;top:0;background:white;}
.nav{
  display:inline;
  margin-left: 10px;
  color:black;
  font-weight: normal;
 
  
}
.nav:hover{background:whitesmoke;}
#links{

 
  list-style-type: none;
 
  padding: 0;
  width: 100%;
  background-color:white;

}
li{
  font-size: 110%;
  display: block;
  color: #000;
  padding: 8px 16px;

  
  
  

}
li:hover{
  
  color:blue;}



.dropp{
  position: relative;
  display: inline;
}
.articles {
  right:0;
  display: none;
  position: absolute;
  background-color:#f9f9f9 ;
  min-width: 300px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
}
.dropp:hover .articles {
  display: block;
}
.hov{
  color:black;
}
.hov:hover{

 color:blue;
 
